	methods:{
		setHeight:function(){
			let _this=this;
			uni.getSystemInfo({
				success:function(res){
					console.log(res)
					_this.$store.commit('setModel',res.model)
					//console.log(_this.$store.state.model)
				}
			})
		},
		statusBarHeight:function(){
			let _this=this;
			uni.getSystemInfo({
				success(res) {
					_this.$store.commit('statusBarHeight',res.statusBarHeight)
				}
			})
			//console.log(_this.$store.state.windowHeight)
		},
		getHeight:function(){
			let _this=this;
			uni.getSystemInfo({
				success(res) {
					_this.$store.commit('windowHeight',res.windowHeight)
				}
			})
			//console.log(_this.$store.state.windowHeight)
		},
		getWidth:function(){
			let _this=this;
			uni.getSystemInfo({
